import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { CommonGlobalVar } from './common-global';
import { UserModel } from './models/user-model.model';
import { Router,RouterStateSnapshot } from '@angular/router';

@Injectable({
  providedIn: 'root'
})

export class AuthenticationService {

  constructor(private http: HttpClient,private router: Router) { }

  verifyModuleLogin(um : UserModel)
  {
    if(um.username == "admin" && um.password == "admin")
    {
      sessionStorage.setItem('currentUser', um.username);
      return true;
    }
    else
    {
      return false;
    }
    // this.http.get(CommonGlobalVar.BASE_API_URL + "login/login?username="+um.username+"&pass=" + um.password + "&ipaddress=" + um.ipaddress).subscribe(
    //   data => {
    //     let res:any = data; 
    //     console.log(res);
    //   },
    //   err => {
    //     console.log(err);
    //   },() => {
    //   }
    // );;
    
    // console.log(um.username);
  }

  logoutUser()
  {
    //window.location.reload();
    sessionStorage.clear();
    this.router.navigate(["/logged-out"]);
  }

  checkUserLoggedIn()
  {
    if (sessionStorage.getItem('currentUser') != null) {      
      return true;
    }
    return false;
  }

  setReload()
  {
    if (!localStorage.getItem('reload')) { 
      localStorage.setItem('reload', 'no reload') 
      location.reload() 
    } else {
      localStorage.removeItem('reload') 
    }
  }

}
