import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';

@Injectable({
  providedIn: 'root'
})

export class NotificationAlertService {

  constructor(private toastr:ToastrService) { }

  showSuccess(message:string, positionClass:string){
    this.toastr.success(message,'',{
      positionClass: positionClass 
    });            
  }

  showError(message:string, positionClass:string){
    this.toastr.error(message,'',{
      positionClass: positionClass 
    });      ;      
  }

  showInfo(message:string, positionClass:string){
    this.toastr.info(message,'',{
      positionClass: positionClass 
    });            
  }

  showWarning(message:string, positionClass:string){
    this.toastr.warning(message,'',{
      positionClass: positionClass 
    });      
  }
  
}
